import server from '../../apis/server';
import { FETCH_WORDS, GET_WORD, REMOVE_WORD, REMOVE_ALL_WORDS } from '../types';

export const fetchWords = () => async dispatch => {
  console.log('działam');
  try {
    const { data } = await server.get('/api/words');
    dispatch({
      type: FETCH_WORDS,
      payload: data,
    });
  } catch (e) {
    console.log(e);
  }
};

export const getWord = (word, index) => ({
  type: GET_WORD,
  payload: { ...word, index },
});

export const removeWord = (id, index) => ({
  type: REMOVE_WORD,
  payload: { id, index },
});

export const removeAllWords = () => ({
  type: REMOVE_ALL_WORDS,
});
